.reset {
  cursor: pointer;
  border-style: outset;
  border-width: 5px;
  border-color: var(--cell-border);
  background: var(--cell-cover);
  padding: 0;

  &:active {
    border-style: inset;
  }

  span {
    display: none;
  }

  &:before {
    content: 'üòê';
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    font-size: 2.5em;
  }

  &:hover:before {
    content: 'ü§™';
  }

  &--look-left:before {
    content: 'ü§î';
  }

  &--look-right:before {
    content: 'ü§î';
    transform: scaleX(-1);
  }

  &--shock:before {
    content: 'üò≤';
    transform: none;
  }

  &--god:before {
    content: 'üòà';
  }

  &--loose:before {
    content: 'üòµ'
  }

  &--win:before {
    content: 'üòé'
  }

  &:active:before {
    content: 'ü§¨';
  }
}